.main-nav {
  position: relative;
  order: 2;
  .offset(0 20px; 20px 0 20px 0);

  @media (min-width: @tablet-width) {
    order: 3;
  }
}

.main-nav__list {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  .offset(0; 0);
  list-style-type: none;
  text-transform: uppercase;
  text-align: center;
  transition: all 0.7s ease;

  @media (min-width: @tablet-width) {
    flex-direction: row;
    justify-content: flex-end;
  }
}

.main-nav__button-toggle {
  position: absolute;
  top: -146px;
  right: 0;
  .offset(0; 0);
  width: 36px;
  height: 36px;
  background-color: @blue1;
  border-radius: 6px;
  transition: background-color 0.4s ease;

  &::before,
  &::after {
    content: "";
    position: absolute;
  }

  &:hover,
  &:focus {
    background-color: @blue2;
    transform: none;
  }

  @media (min-width: @tablet-width) {
    top: -90px;
    right: 0;
  }

  @media (min-width: @desktop-width) {
    top: -100px;
  }
}

.main-nav--closed .main-nav__button-toggle::before {
  top: 17px;
  left: 10px;
  width: 17px;
  height: 2px;
  background-color: @white;
  box-shadow: 0 -4px @white, 0 4px @white;
  transition: 0.4s ease;
}

.main-nav--opened .main-nav__button-toggle {
  &::before,
  &::after {
    top: 17px;
    left: 8px;
    width: 19px;
    height: 2px;
    background-color: @white;
    transition: 0.4s ease;
  }

  &::before {
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(-45deg);
  }
}

.main-nav--closed .main-nav__list {
  visibility: hidden;
  opacity: 0;
  transform: translateX(-100px);
  width: 0;
  height: 0;
}

.main-nav--opened .main-nav__list {
  visibility: visible;
  opacity: 1;
  transform: translateX(0);
  width: auto;
  height: auto;
}

.main-nav__link {
  display: inline-block;
  .offset(5px; 0);
  .typography(18px; 24px);
  color: @blue1;
  transition: 0.3s ease;

  &:hover,
  &:focus {
    color: @blue2;
  }

  &:active {
    opacity: 0.7;
  }

  @media (min-width: @tablet-width) {
    padding: 10px;
    font-weight: 500;
    .typography(14px; 20px);
  }
}
